<template>
	<div class="page1_1_1">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
			<div class="fr inter">
				<interBtn title1="审计目的及重点" title="审计方向" id="more1_1_3" ydname="审计准备"/>
			</div>
		</div>
		<!--词云弹出框-->
		<div class="am-modal am-modal-no-btn" tabindex="-1" id="doc-modal-cy">
		    <div class="am-modal-dialog">
		        <div class="am-modal-hd modal-head">
		            <ul class="list-cy">
		                <li class="active">符合性词云</li>
		                <li>欠缺性词云</li>
		                <li>特殊性词云</li>
		            </ul>
		            <a href="javascript: void(0)" class="am-close am-close-spin" data-am-modal-close>&times;</a>
		        </div>
		        <div class="modal-content">
		            <div class="cont xsdcy">

		            </div>
		            <div class="cont qqxcy hidden">

		            </div>
		            <div class="cont dyxcy hidden">
		            </div>
		        </div>
		    </div>
		</div>
		<!--词云弹出框-->
		<div class="check-form">
		    <div>
		        <div class="my-tabs box-border-shadow" id="doc-my-tabs">
		        	<div class="ts tishi-word">上传相关后续工作纪要文档，与前期工作纪要比较</div>
		            <ul class="my-tabs-nav" style="margin-top: 40px;">
		                <li class="active banner-btn">第一步：选取要上传的文档</li>
		                <li class="banner-btn">第二步：查重结果展示</li>
		                <!--<li class="banner-btn">第三步：上传文档确认</li>-->
		            </ul>
		            <div class="my-tabs-bd">
		                <div class="my-tab-panel" style="width: 70%;">
		                    <form class="am-form form">
		                        <div class="form-group">
		                            <label for="title" class="form-label">文档标题:</label>
		                            <div class="form-input">
		                                <input type="text" placeholder="填写真实文档标题，不超过50字" name="title" id="title"/>
		                            </div>
		                        </div>
		                        <div class="form-group">
		                            <label for="author" class="form-label">文档作者:</label>
		                            <div class="form-input">
		                                <input type="text" placeholder="填写真实作者，不超过15字  [多作者用英文”,”逗号分隔开]" name="author" id="author"/>
		                            </div>
		                        </div>
		                        <div class="form-group">
		                            <label for="date" class="form-label">上传时间:</label>
		                            <div class="form-input">
		                                <input type="date" name="date" id="date"/>
		                            </div>
		                        </div>
		                        <div class="form-group">
		                            <label for="word1" class="form-label">目标词汇:</label>
		                            <div class="form-input">
		                                <input type="text" placeholder="添加目标词汇  [多词汇用英文”,”逗号分隔开，单个词汇必须以英文”,”逗号结尾]" name="word1" id="word1"/>
		                            </div>
		                        </div>
		                        <div class="form-group">
		                            <label for="word2" class="form-label">停用词汇:</label>
		                            <div class="form-input">
		                                <input type="text" placeholder="添加停用词汇  [多词汇用英文”,”逗号分隔开，单个词汇必须以英文”,”逗号结尾]" name="word2" id="word2"/>
		                            </div>
		                        </div>
		                        <div class="form-group">
		                            <label for="type" class="form-label">文本类型:</label>
		                            <div class="form-input">
		                                <select id="type" class="form-input">
		                                    <option>请选择文本类型</option>
		                                    <option>.txt文件</option>
		                                    <option>.docx文件</option>
		                                    <option>.doc文件</option>
		                                </select>
		                            </div>
		                        </div>
		                        <div class="form-group form-upload">
		                            <label for="fileName" class="form-label">选择文档:</label>
		                            <div class="form-input">
		                                <div class="clearfix">
		                                    <input class="file-name" id="fileName" disabled="disabled" type="text" placeholder="请上传相关后续工作纪要文档，且小于15M">
		                                    <div class="upload-up-btn">
		                                        <input type="file" name="paperFile" id="paperFile" class="data">上传待测文档
		                                    </div>
		                                </div>
		                                <div class="matter">
		                                    <p style="margin: 10px 0 25px">
		                                        注意事项：<br/>
		                                        1、超过15万字，请拆分成多篇检测！<br/>
		                                        2、请在上传文档前自行删除文档中的图片、表格、公司、代码，以免影响检测结果！<br/>
		                                        3、有效文档格式：WORD文件(.doc .docx)文本文件(.txt)！<br/>
		                                    </p>
		                                </div>
		                            </div>

		                        </div>
		                        <div id="submit" style="text-align:center">
		                        	<div class="img3 img_btn">与前期工作纪要比较</div>
		                        </div>
		                    </form>

		                </div>
		                <div class="my-tab-panel hidden">
		                    <div class="xinxi" style="text-align: center">
		                        请先完成第一步提交相关信息
		                    </div>
		                    <div class="am-g hidden c-contain" style="position: relative">
		                        <div class="color-img" style="text-align:center;margin-bottom:40px;height:100px;line-height:100px">
		                            <span class="color-font" style="color:#ff7733">高</span>
		                            <div class="color-box">
		                                <!--模块拖拽--> <div class="drag"></div>
		                            </div>
		                            <span class="color-font" style="color:#e0e0e0">低</span>
		                        </div>
		                        <div class="am-u-md-6">
		                            <div class="am-panel-group" id="accordion2">
		                                <div class="am-panel am-panel-default">
		                                    <div class="am-panel-hd">
		                                        <h4 class="am-panel-title" data-am-collapse="{parent: '#accordion2', target: '#do-not-say-11'}">

		                                        </h4>
		                                    </div>
		                                    <div id="do-not-say-11" class="am-panel-collapse am-collapse">
		                                        <div class="am-panel-bd" style="text-align: justify">

		                                        </div>
		                                    </div>
		                                </div>
		                            </div>
		                            <div class="btn-group-find">
		                                <span class="btn-find light c-find">整篇查询</span>
		                                <span class="btn-find p-find">整段查询</span>
		                                <!--<span class="btn-find s-find">整句查询</span>-->
		                            </div>
		                        </div>
		                        <div class="am-panel-group am-u-md-6" id="accordion">

		                        </div>
		                        <div class="hidden model-onload" style="width: 100%;height: 100%;background: rgba(0,0,0,0.5);position: absolute;top:0;left: 0;">
		                            <div class="onload-icon" style="text-align: center;height:100%;line-height:600px;width: 100%;color: #fff;">
		                                <i class="am-icon-spinner am-icon-pulse am-icon-lg"></i> 查重结果加载中...（大约剩余时间为
		                                <span id="sy-time-p" style="display:inline-block;color: #0c79b1;width:26px;text-align: center"></span> 秒）</div>
		                        </div>
		                    </div>

		                </div>
		            </div>
		        </div>
		    </div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import interBtn from './../interactions'
	import amazeui from 'amazeui';
	require('amazeui/dist/css/amazeui.css');
	import * as echarts from 'echarts';
	require('echarts-wordcloud');
	// require('../../../assets/js/layer.js');
	export default{
		name:'page1_1_1',
		data(){
			return{
				msg:["审计准备","审计方向及重点","后续工作纪要"],
				company:JSON.parse(sessionStorage.getItem("company")),
				form: {
					title:'',
		            name: '',
		          	date: '',
		          	word1: '',
		          	word2: '',
		          	type: '',
		        }
			}
		},
		components:{
			Place,
			interBtn
		},
		computed:{
			companyName(){
				return JSON.parse(this.$store.getters.getCompany)
			}
		},
		mounted: function(){
			 $('#doc-modal-cy .list-cy li').click(function () {
		        $(this).addClass('active').siblings('li').removeClass('active');
		        var i=$(this).index()+1;
		        $('#doc-modal-cy .modal-content .cont:nth-child('+i+')').removeClass('hidden').siblings('.cont').addClass('hidden');
		    })


			  $(function() {

        //        nav
        $('#doc-my-tabs .my-tabs-nav li').click(function () {
            var num=$(this).index();
            $(this).addClass('active').siblings('li').removeClass('active');
            $('#doc-my-tabs .my-tabs-bd .my-tab-panel:nth-child('+(num+1)+')').removeClass('hidden').siblings('.my-tab-panel').addClass('hidden');
        });
        $(".form input[name='word1']").blur(function () {
            if($(".form input[name='word1']").val().length>0){
                if($(this).val().indexOf(",")<0&&$(this).val().indexOf("，")<0){
                    console.log($(".form input[name='word1']").val().length);
                    alert('请检查目标词汇填写格式是否正确，单个词汇必须以","结尾');
                }


            }
        });
        $(".form input[name='word2']").blur(function () {
            if($(".form input[name='word2']").val().length>0){
                if($(this).val().indexOf(",")<0&&$(this).val().indexOf("，")<0){
                    console.log($(".form input[name='word2']").val().length);
                    alert('请检查目标词汇填写格式是否正确，单个词汇必须以","结尾');
                }


            }
        });


//            上传文档
        function fileVerificat(file){
            if (file.indexOf("txt") != -1||file.indexOf("docx") != -1||file.indexOf("doc") != -1||file.indexOf("pdf") != -1){
                return true;
            } else {
                return false;
            }
        }
        $(".form-upload").each(function(){
            var fileNames = $(this).find('#fileName');
            $(this).find('.upload-up-btn').on("change","#paperFile",function(){
                var filePath=$(this).val();
//                                var arr=filePath.split('\\');
//                                var fileName=arr[arr.length-1];
//                                fileNames.val(filePath);
                if(fileVerificat(filePath)){
                    var arr=filePath.split('\\');
                    var fileName=arr[arr.length-1];
                    fileNames.val(filePath);
                }else{
                    fileNames.val("您未上传文件，或者上传文件类型有误").show();
                    return false
                }
            });
        })

//            表单校验
        function checkFileForm(){
//                var isStr=new RegExp("[ ,\\`,\\~,\#,\\%,\\^,\\&,\\\\,\\/,\\?,\\:,\\=,\"]");
            var title=$(".form input[name='title']").val().trim();
            var author=$(".form input[name='author']").val().trim();
            var date=$(".form input[name='date']").val();
            var type=$(".form input[name='type']").val();


            if(title==null||title==''){
                alert('标题不能为空，请重新输入');
                return false;
            }
            if(title.length>60){
                alert('标题不能大于60个字符，请重新输入');
                return false;
            }
            if(author==null||author==''){
                alert('作者不能为空，请重新输入');
                return false;
            }
            if(author.length>15){
                alert('作者不能大于15个字符，请重新输入');
                return false;
            }
            if(date==null||date==''){
                alert('请选择上传时间');
                return false;
            }
            if($(".form input[name='word1']").val().length>0){
                if($(".form input[name='word1']").val().indexOf(",")<0&&$(".form input[name='word1']").val().indexOf("，")<0){
                    console.log($(".form input[name='word1']").val().length);
                    alert('请检查目标词汇填写格式是否正确，单个词汇必须以","结尾');
                    return false;
                }
            }
            if($(".form input[name='word2']").val().length>0){
                if($(".form input[name='word2']").val().indexOf(",")<0&&$(".form input[name='word2']").val().indexOf("，")<0){
                    console.log($(".form input[name='word2']").val().length);
                    alert('请检查目标词汇填写格式是否正确，单个词汇必须以","结尾');
                    return false;
                }
            }
            if(type!=null||type==''){
                alert('请选择属性分类');
                return false;
            }
            var paperFile=$("#fileName").val();
            if(paperFile==null||paperFile==""){
                alert('检查文档不能为空，请重新选择');
                return false;
            }
            var fileText=paperFile.substring(paperFile.lastIndexOf("."),paperFile.length);
            var fileName=fileText.toLowerCase();
            if((fileName!='.txt')&&(fileName!='.doc')&&(fileName!='.docx')) {
                alert("文件格式不正确，请选择txt、doc、docx文件进行上传");
                $("#paperFile").focus();
                return false;
            };
            return true;
        }
        // al = function (msg) {
        //     layer.alert(msg, {icon: 2});
        // };
        // alert_succ_show = function (msg) {
        //     layer.alert(msg, {icon: 1});
        // };


        var filesText;
        //js 读取文件
        $('#submit .img1').click(function () {
            $.ajax({
                url: "http://121.42.29.188:10000/ch_chachong_time",
                type: 'post',
                async: true,
                dataType: "json",
                data: {"id": 1,biaoshi:1},
                success: function (result) {
                    var a=parseInt(result.data.run_time)+50;
                    aa(a,1);

                }
            })

        });
        $('#submit .img2').click(function () {
            $.ajax({
                url: "http://121.42.29.188:10000/ch_chachong_time",
                type: 'post',
                async: true,
                dataType: "json",
                data: {"id": 1,biaoshi:2},
                success: function (result) {
                    var a=parseInt(result.data.run_time)+50;
                    aa(a,2);

                }
            })

        });
//获取file里的文件，并转成字符串，调用cctitle()方法
        function aa(a,biaoshi){
            if(checkFileForm()){
                var files=document.getElementById("paperFile").files[0];


                var reader = new FileReader();//new一个FileReader实例
                //判断文件类型，是不是text类型
                if (/text+/.test(files.type)||/application+/.test(files.type)) {
                    //                            显示第一步隐藏第二步
                    $('.xinxi').html('<div class="onload-icon" style="text-align: center;height:400px;line-height:340px;width: 100%">' +
                        '<i class="am-icon-spinner am-icon-pulse am-icon-lg"></i> ' +
                        '查重结果加载中...（大约剩余时间为 <span id="sy-time" style="display:inline-block;color: #0c79b1;width:26px;text-align: center"></span> 秒）</div>');

                    $('.c-contain').addClass('hidden');
                    if(/application+/.test(files.type)){
//                        InputStreamReader isr=new InputStreamReader(new FileInputStream(fileName),"UTF-8");
                        reader.readAsBinaryString(files);
                    }else{
                        reader.readAsText(files, "gb2312");
                    }

                    reader.onload = function() {
//                            console.log(this.result);
                        //            将所有面板折叠起来
                        $('.am-panel-group').find('.am-in').removeClass('am-in');

                        $('#doc-my-tabs .my-tabs-nav li:nth-child(2)').addClass('active').siblings('li').removeClass('active');
                        $('#doc-my-tabs .my-tabs-bd .my-tab-panel:nth-child(2)').removeClass('hidden').siblings('.my-tab-panel').addClass('hidden');
                        filesText=this.result;
                        console.log(filesText);

                        cctitle(a);

                    }
                }else{
                    alert('请上传文档');
                }
            }


            //        词云
            function wordCloud(data) {
                console.log(data);
                return {
                    title:{
                        text:"",
                    },
                    tooltip: {},
                    series: [{
                        type: 'wordCloud',
//                    gridSize: 20,
                        sizeRange: [18, 50],
                        rotationRange: [0, 45],
                        shape: 'circle',
                        textStyle: {
                            normal: {
                                color: function() {
                                    return 'rgb(' + [
                                        Math.round(Math.random() * 160),
                                        Math.round(Math.random() * 160),
                                        Math.round(Math.random() * 160)
                                    ].join(',') + ')';
                                }
                            },
                            emphasis: {
                                shadowBlur: 10,
                                shadowColor: '#333'
                            }
                        },
                        data: data
                    }]
                };

            }
//            显示参考文档标题
            function cctitle(a) {
                var myVar=setInterval(function(){
//                                console.log(i);
                    if(a>=0){
                        if(a>0){
                            a--;
                        }else{
                            a=0
                        }
                        $('#sy-time').text(a);
                    }else{
                        $('#sy-time').text(0);
                        console.log(a);
                    }
                },1000);



                //                清空参考文档
                $('#accordion').empty();
                var title=$(".form input[name='title']").val().trim();
                var author=$(".form input[name='author']").val().trim();
                var time=$(".form input[name='date']").val();
                var reg=new RegExp("，","g");
                var word1=$(".form input[name='word1']").val()?$(".form input[name='word1']").val().replace(reg,","):" ";
                var word2=$(".form input[name='word2']").val()?$(".form input[name='word2']").val().replace(reg,","):" ";
                //                显示查重结果参考文档标题接口
                $.ajax({
                    url:"http://121.42.29.188:10000/ch_chachong",
                    type:'post',
                    async:true,
                    dataType: "json",
                    data:{"biaoshi":biaoshi,"text":filesText,"title":title,"author":author,"time":time,"word1":word1,"word2":word2},
                    success:function(result){
                        console.log(result.data);
//                            显示第二步隐藏第一步
                        clearInterval(myVar);
                        $('.xinxi').html('');
                        $('.c-contain').removeClass('hidden');
                        //写入源文件标题
                        $('#accordion2 .am-panel .am-panel-title').text($(".form input[name='title']").val().trim());
                        $('#accordion2 .am-panel .am-panel-bd').html(filesText);//写入源文件内容


                        //写入参考文件标题
                        for(var i=0;i<result.data.length;i++){
                            $('#accordion').append('<div class="am-panel am-panel-default">\n' +
                                '                                <div class="am-panel-hd">\n' +
                                '                                    <h4 class="am-panel-title" id="'+result.data[i].id+'" data-am-collapse="{parent: \'#accordion\', target: \'#do-not-say-'+i+'\'}">\n' + result.data[i].icon +
                                '                                    </h4>' +
                                '                                    <div class="panel-bs">（<span class="bs xsbs click">相似度：'+(result.data[i].num*100).toFixed(1)+'% </span> , ' +
                                '<span class="bs qqbs">欠缺性 ：'+(result.data[i].qianque_num*100).toFixed(1)+'%</span> , ' +
                                '<span class="bs dybs">多余性 ：'+(result.data[i].duoyv_num*100).toFixed(1)+'%</span> , ' +
                                '<span class="cyBtn" data-am-modal="{target: \'#doc-modal-cy\', closeViaDimmer: 0}"> 词云 </span> ' +
                                '）</div>' +
                                '                                </div>\n' +
                                '                                <div id="do-not-say-'+i+'" class="am-panel-collapse am-collapse">\n' +
                                '                                    <div class="am-panel-bd" style="text-align: justify">\n' +
                                '\n' +
                                '                                    </div>\n' +
                                '                                </div>\n' +
                                '                            </div>');
                        }

                        //写入参考文件标题
//                            $('#accordion .am-panel').each(function (i) {
//                                var $this=$(this);
//                                $this.find('.am-panel-title').html(result.data[i].icon+" <div style='color: #00a2c9;font-size: 15px;text-align: right'>（相似度："+result.data[i].num.toFixed(2)*100+"%）</div>" );
//                                $this.find('.am-panel-title').attr('id',result.data[i].id);
//                            });

//                    展示相似度、多余性、欠缺性三者的词云图
                        $('#accordion .am-panel').find('.cyBtn').click(function () {

                            var bs_data=result.data[$(this).parents('.am-panel').index()].bs;
                            var qianque_bs_data=result.data[$(this).parents('.am-panel').index()].qianque_bs;
                            var duoyv_bs=result.data[$(this).parents('.am-panel').index()].duoyv_bs;
                            $('.xsdcy').empty();
                            $('.qqxcy').empty();
                            $('.dyxcy').empty();

                            if(bs_data.length>0){
                                $('.xsdcy').append('<div style="width: 900px;height: 600px;margin: 0 auto;" id="xsd"></div>');
                                var myEchart1=echarts.init(document.getElementById("xsd"));
                                myEchart1.setOption(wordCloud(bs_data));
                            }else{
                                $('.xsdcy').append('<div style="width: 100%;height: 100%;text-align: center;line-height: 600px;font-size: 30px;color: #737373">相似度为0%</div>')
                            }
                            if(qianque_bs_data.length>0){
                                $('.qqxcy').append('<div style="width: 900px;height: 600px;margin: 0 auto;" id="qqx"></div>');
                                var myEchart2=echarts.init(document.getElementById("qqx"));
                                myEchart2.setOption(wordCloud(qianque_bs_data));
                            }else{
                                $('.qqxcy').append('<div style="width: 100%;height: 100%;text-align: center;line-height: 600px;font-size: 30px;color: #737373">欠缺性为0%</div>')

                            }
                            if(result.data[$(this).parents('.am-panel').index()].duoyv_bs.length>0){
                                $('.dyxcy').append('<div style="width: 900px;height: 600px;margin: 0 auto;" id="dyx"></div>');
                                var myEchart3=echarts.init(document.getElementById("dyx"));
                                myEchart3.setOption(wordCloud(duoyv_bs));
                            }else{
                                $('.dyxcy').append('<div style="width: 100%;height: 100%;text-align: center;line-height: 600px;font-size: 30px;color: #737373">多余性为0%</div>')

                            }

                        })



//                    相似度、多余性、欠缺性三者间的切换
                        $('#accordion .am-panel').find('.am-panel-title').click(function () {
                            var id=$(this).attr('id');
                            if($(this).parents('.am-panel').find('.xsbs').hasClass('click')){
                                var list1=result.data[$(this).parents('.am-panel').index()].bs;
                                ccxq(id,list1,$(this).parents('.am-panel'),filesText,0);
                            }else if($(this).parents('.am-panel').find('.qqbs').hasClass('click')){
                                var list2=result.data[$(this).parents('.am-panel').index()].qianque_bs;
